  .hero {
    min-width: 100%;
  }

  .hero-text h1 {
    font-size: 2.5rem;
  }

  .hero-text p {
    font-size: 1.2rem;
  }
}

@media screen and (max-width: 992px) {
  .hero {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    max-height: 600px;
  }
  .hero-text h1 {
    font-size: 2rem;
  }

  .hero-text p {
    font-size: 1rem;
  }

  .nav-links {
    display: none;
  }

  .hamburger-menu {
    display: block;
  }

  .nav-drawer {
    display: block;
  }

  .navbar {
    background-color: #2be72b;
    padding: 15px;
  }

  .restaurant-list {
    margin: 40px 20px;
  }

  .hero-text h1 {
    font-size: 2.5rem;
  }

  .hero-text p {
    font-size: 1.2rem;
  }
